/**
 * Section import. Files named after the row's wrapping class name.
 */
.header {
  position: fixed;
  z-index: 100;
  background: #FFFFFF;
  width: 100%;
  height: 62px;
  top: 0;
  box-shadow: 0 0 10px 0px rgba(0, 0, 0, 0.2);
  padding: 5px;
}
.header .pad-left {
  margin-left: 1em;
}
.header .nav {
  margin-top: 16px;
  font-size: 1em;
}
@media only screen and (min-width: 1140px) {
  .header .nav {
    font-size: 1.25em;
    margin-top: 14px;
  }
}
.header .nav a {
  text-decoration: none;
  font-weight: bold;
  margin-left: 0.5em;
}
.header .hamburger {
  background-image: url("../images/hamburger.svg");
  background-size: cover;
  font-size: 0;
  display: inline-block;
  width: 30px;
  height: 30px;
  float: right;
  margin-top: 10px;
  margin-right: 4px;
}
.header .hamburger-menu {
  box-shadow: 0 4px 10px -4px rgba(0, 0, 0, 0.2);
  background: #FFFFFF;
  width: calc(100% + 10px);
  margin: 7px -5px 0;
}
.header .hamburger-menu a {
  display: block;
  border-bottom: 1px solid #F0F0F0;
  text-align: center;
  font-size: 1.5em;
  line-height: 2.5em;
  text-decoration: none;
}
.header h1 {
  margin: 0;
  text-align: right;
  display: inline-block;
  vertical-align: middle;
  font-size: 1.25rem;
}
@media only screen and (min-width: 740px) {
  .header h1 {
    text-align: center;
    margin-top: 8px;
    font-size: 2rem;
  }
}
.header img {
  max-width: 100%;
  max-height: 50px;
  display: inline-block;
  vertical-align: middle;
}

body {
  margin-top: 60px;
}

.conversation-count {
  background-color: #F0F0F0;
  text-align: center;
  position: relative;
  padding: 2em 0;
}
.conversation-count:after {
  content: " ";
  position: absolute;
  width: 0;
  height: 0;
  z-index: 10;
}
.conversation-count:after {
  left: calc(50% - 20px);
  bottom: -20px;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
  border-top: 20px solid #F0F0F0;
}

.message-box {
  background-color: #EC1C24;
  border-radius: 0.5em;
  color: #FFFFFF;
  padding: 0.5em 0;
  line-height: 1em;
  font-weight: bold;
  font-size: 3em;
}
.message-box:after {
  display: block;
  background-image: url("../images/chat-pip.png");
  content: " ";
  width: 40px;
  height: 30px;
  position: absolute;
  right: 1.5rem;
  bottom: -22px;
  background-repeat: no-repeat;
}
